.btn {
  font: inherit;
  color: white;
  border: none;
  border-radius: 25px;
  align-items: center;
  font-weight: bold;
  background-color: #4d1601;
  display: flex;
  padding: 0.01rem 3rem;
  justify-content: space-around;
  cursor: pointer;
}

.btn:hover, btn:active {
  background-color: #2c0d00;
}

.icon {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.5rem;
}

.badge {
  background-color: #b94517;
  padding: 0.25rem 1rem;
  border-radius: 25px;
  margin-left: 1rem;
  font-weight: bold;
}

.btn:hover .badge, btn:active .badge {
  background-color: #92320c;
}

.bump{
  animation: bump 200ms ease-out;
}

@keyframes bump {
  0%{
    transform: scale(1);
  }
  10%{
    transform: scale(0.9);
  }
  30%{
    transform: scale(1.1);
  }
  50%{
    transform: scale(1.15);
  }
  100%{
    transform: scale(1);
  }
}